export default {
    onShow(){
        this.getgoodsList()
    },
    data(){
        return {
            goodsList: []
        }
    },
    methods: {
        getgoodsList(){
            this.$http.getgoodsList().then(res=>{
                if(res.status == 200) {
                    this.goodsList = res.data
                }
            })
        },
        
    },
}
